.header-section {
background-color: #FFB8CA;
}
    
 
.header-section__mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (min-width: 480px) {
      max-width: 767px;
      padding-left: 34px;
      padding-right: 34px;
    }
    @media screen and (min-width: 768px) {
      // max-width: 1279px;
      padding-top: 38px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1280px;
      padding-left: 46px;
      padding-right: 115px;
      
        }
  }
.logo {
  @media screen and (max-width: 767px){
 padding: 6px;
  }
}
.header-section__burger-menu {
background-color: transparent;
border: none;
cursor: pointer;
@media screen and (min-width: 768px) {
  margin-right: 30px;
}
@media screen and (min-width: 1280px) {
  display: none;
}
}

  .header-section__submit-button {
    width: 160px;
    height: 44px;
    border-radius: 22px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.312;
    display: block;
    margin: 0 auto;
    border:none;
    background-color: $text-color;
    color: $title-color;
