    }
    @media screen and (max-width:767px) {
      display: none;
      
    }
}

.header-section__submit-button-mob {
  width: 160px;
    height: 44px;
    border-radius: 22px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.312;
    display: block;
    margin: 0 auto;
    border:none;
    background-color: $text-color;
    color: $title-color;
    position: relative;
    cursor: pointer;
    padding-left: 48px;
    padding-right: 48px;
    justify-content: space-between;
    margin-bottom: 19px;
    &:hover,
